import Engine from './utils/engine';

const app = new Engine(document.getElementById('canvas'), {
  debug: false,
  width: 480,
  height: 480
});

function drawDot(ctx, x, y, size = 1) {
  ctx.save();
  ctx.beginPath();
  ctx.arc(x, y, size, 0, Math.PI * 2);
  ctx.fill();
  ctx.restore();
}

function drawCircle(ctx, x, y, size, segments = 3, fill = false, color = 'red') {
  let slice = ((Math.PI * 2) / segments);

  ctx.save();
  ctx.strokeStyle = color;
  ctx.fillStyle = color;
  ctx.beginPath()
  for (let i = 0; i <= segments; i++) {
    let angle = slice * i;
    let _x = Math.cos(angle) * size;
    let _y = Math.sin(angle) * size;

    if (i == 0) {
      ctx.moveTo(_x + x, _y + y);
    } else {
      ctx.lineTo(_x + x, _y + y);
    }
  }
  if (fill) {
    ctx.fill();
  }
  else {
    ctx.stroke();
  }

  ctx.restore();
}

const colors = ['red', 'blue', 'green'];

const getColor = (index) => {
  return colors[index % colors.length];
}

app.onRender((ctx, { frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);

  ctx.save();
  ctx.translate(width / 2, height / 2);
  ctx.rotate(Math.PI * 0.5);
  drawCircle(ctx, 0, 0, width / 10, 3, true, getColor(0));
  drawCircle(ctx, 0, 0, width / 10, 4, false, getColor(1));
  drawCircle(ctx, 0, 0, width / 10, 5, false, getColor(2));

  drawCircle(ctx, 0, 0, width / 5, 5, false, getColor(3));
  drawCircle(ctx, 0, 0, width / 4, 6, false, getColor(4));
  drawCircle(ctx, 0, 0, width / 3, 7, false, getColor(5));
  drawCircle(ctx, 0, 0, width / 2, 8, false, getColor(6));
  ctx.restore();
});

app.start();
